.cardsProjetos{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 85%;
    box-shadow: 1px 1px 15px 10px rgba(0,0,0,1);
    border-radius: 10px;
}

.cardsProjetos-left{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
}
.cardsProjetos-left h2{
    margin-top: 10px;
    margin-bottom: 20px;
    color: #fff;
}
.cardsProjetos-left p{
    padding: 10px;
    text-align: justify;
    color: #c4c4c4;
}
.cardsProjetos-left a{
    margin-top: 10px;
    color: #8A2BE2;
    transition: .7s;
}
.cardsProjetos-left a:hover{
    opacity: 0.5;
}
.cardsProjetos-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}
.cardsProjetos-info .icon{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 50px;
    margin-top: 10px;
}
.cardsProjetos-info .icon span{
    font-size: 15px;
    font-family: cursive;
}
.cardsProjetos-info button{
    margin-top: 10px;
    padding: 10px 40px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    transition: .7s;
}
.cardsProjetos-info button:hover{
    opacity: 0.5;
}

.cardsProjetos-right{
    background-color: blue;
    width: 70%;
}
.cardsProjetos-right img{
    width: 100%;
    height: 100%;
    background-size: contain;
}


@media (max-width: 1120px){
    .cardsProjetos{
        display: flex;
        flex-direction: column-reverse;
    }
    .cardsProjetos-left{
        width: 100%;
    }
    .cardsProjetos-left button{
        margin-bottom: 10px;
    }
    .cardsProjetos-right{
        width: 100%;
        height: 100%;
    }
}