.footer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    background-color: black;
    color: #fff;
    width: 100%;
    padding: 10px;
}
.footer-left{
    display: flex;
}
.footer p{
    display: flex;
    align-items: center;
    
}
.footer-menu{
    display: flex;
}

.footer-menu a{
    color: #fff;
    font-size: 20px;
    margin-left: 10px;
    list-style: none;
    cursor: pointer;
    transition: .7s;
}
.footer-menu a:hover{
    opacity: 0.5;
}
