.skillsCards{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    font-size: 50px;
    border-radius: 10px;
    box-shadow: 1px 1px 20px 1px rgba(0,0,0,1);
    width: 300px;
    height: 200px;
    background-color: #141414;
}

.skillsCards:hover{
    opacity: 0.5;
    transform: scale(1.2, 1.2);
}

.skillsCards p{
    color: #8A2BE2;
    font-size: 25px;
    font-family: cursive;
    font-weight: bold;
    margin-top: 20px;
}