.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    width: 60%;
    margin-top: 20px;
}
.header h2{
    color: #fff;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
span{
    display: flex;
    align-items: center;
    font-size: 40px;
    letter-spacing: 5px;
    font-weight: bold;
}
.bracket{
    color: #8A2BE2;
}
.menu{
    display: flex;
    align-items: center;
}

.menu a{
    color: #fff;
    margin-left: 30px;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    transition: .7s;
}
.menu a.active{
    border-bottom: 3px solid #8A2BE2;
    margin-top: 3px;
    transition: .2s;
}
.menu a:hover{
    opacity: 0.7;
}

.menu-hamburguer{
    display: none;
    height: 100%;
}

.menu-hamburguer-links{
    position: fixed;
    background-color: #fff;
    height: 500px;
    width: 250px;
    top: 0;
    right: 0;
}

.disable{
    display: none;
}

.a{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.a a{
    font-family: cursive, sans-serif;
    text-transform: uppercase;
    margin-top: 30px;
    font-size: 25px;
    font-weight: bold;
}

@media(max-width: 620px){
    .menu-hamburguer{
        display: flex;
        font-size: 40px;
    }
}