.projetos{
    width: 60%;
    height: 200vh;
    margin-top: 30px;
}

.projetos-container{
    width: 100%;
    height: 40%;
}

@media (max-width: 1630px){
    .projetos{
        width: 80%;
    }
}

@media (max-width: 1120px){
    .projetos{
        height: 350vh;
    }
    .projetos-container{
        height: 45%;
    }
}