.home{
    display: flex;
    margin-top: 50px;
    width: 60%;
    height: 100%;
}

.home-left{
    color: #FFF;
    width: 50%;
    height: 100%;
}

.home-left p{
    opacity: 0.7;
}

.home-left h1{
    font-size: 80px;
    font-family: cursive;
    width: 30%;
}

.home-left h3{
    font-size: 40px;
    font-weight: 500;
    width: 50%;
    color: rgba(255, 255, 255, 0.7);
}

.frontend-icon{
    display: inline-block;
    color: #8A2BE2;
}

.home-right{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 50%;
}

.home-right img{
    width: 500px;
    height: 400px;
}

.sobre{
    color: #FFF;
    width: 60%;
    margin-bottom: 80px;
    height: 80%;
}

.sobre p{
    text-align: justify;
    font-size: 20px;
    margin-bottom: 20px;
    color: rgba(255, 255, 255, 0.7);
}

.sobre .techs{
    display: inline-block;
    font-size: 18px;
    font-family: cursive;
    color: #fff;
}

.sobre a{
    color: #8A2BE2;
    text-decoration: none;
    cursor: pointer;
    transition: .8s;
}
.sobre a:hover{
    opacity: 0.5;
}

@media(max-width: 1630px){
    .home{
        width: 80%;
    }
    .home-left h3{
        width: 60%;
    }
    .sobre{
        margin-bottom: 0;
        width: 80%;
    }
    .sobre p{
        font-size: 18px;
    }
    .header{
        width: 80%;
    }
    .footer{
        padding: 0;
    }
}

@media(max-width: 1165px){
    .home{
        height: 40%;
        margin-top: 20px;
    }
    .home-left h1{
        font-size: 40px;
    }
    .home-left h3{
        font-size: 25px;
    }
    .home-left span{
        font-size: 30px;
    }
    .home-right img{
        max-width: 400px;
        max-height: 250px;
    }
    .sobre{
        margin-top: 0;
        height: 30%;
    }
    .sobre p{
        font-size: 15px;
        margin-bottom: 8px;
    }
    .sobre .techs{
        font-size: 15px;
    }
    .footer{
        font-size: 10px;
    }
}

@media(max-width: 890px){
    .home{
        height: 30%;
        width: 80%;
        margin-top: 40px;
    }
    .home-left h1{
        width: 100%;
    }
    .home-left h3{
        width: 100%;
    }
    .home-left{
        width: 100%;
    }
    .home-right img{
        display: none;
    }
    .sobre p{
        margin-top: 30px;
    }
}

@media (max-width: 620px){
    .menu{
        display: none;
    }
    .footer{
        width: 100%;
    }

}

@media (max-width: 420px){
    .home{
        height: 15%;
    }
    .home-left h1{
        width: 100%;
        font-size: 30px;
    }
    .home-left h3{
        width: 100%;
        font-size: 12px;
    }
    .home-left span{
        font-size: 10px;
    }
    .sobre h3{
        font-size: 20px;
    }
    .sobre span{
        font-size: 20px;
    }
    .sobre p{
        font-size: 15px;
    }
    .sobre .techs{
        font-size: 12px;
    }
    .footer{
        width: 100%;
        font-size: 8px;
    }
}

@media(max-width: 375px){
    .sobre p{
        font-size: 12px;
    }
}